html, body {
	margin: 0;
	height: 100%;
}

html {
  font-family: 'Work Sans', sans-serif;
	box-sizing: border-box;
}

body {
  background-color: $dark-purple;
	position: relative;
	display: grid;
	grid-template-rows: 1fr;
	grid-gap: 1em;
}

.main__wrapper {
	position: relative;
	margin-bottom: $nav-height;
}

.logo {
  background-size: 5em;
  background-repeat: no-repeat;
  width: 7em;
  background-position-x: -25px;
  background-position-y: -8px;
  height: $logo-height;
  line-height: 45px;
  background-image: $logo-orange;
  text-align: right;
  font-weight: 500;
  font-size: 18px;
  text-decoration: none;
  color: $purple;
}

.main__inner,
.flash-messages__wrapper {
  padding: 0 2em;
  position: relative;
  color: $white;
  max-width: 750px;
	margin: 0 auto;
	top: $nav-height;
}

header {
	margin-top: 1em;
}

.header__title {
	margin: 0;
}



